<template>
  <div id="home" :style="{ 'background-image': 'url(' + backgroundImageSrc + ')', 'background-size':'cover'}">
    <header class="header">
      
      <div class="headerTitle" :style="{ 'background-image': 'url(' + titleImageSrc + ')','background-size':'cover'}"></div>
      
      <div class="headerNav" :style="{ 'background-color': backgroundColor, 'background-size':'cover' }">
      <nav>
        <ul v-show="$route.path !== '/'" class="b">
          <li v-show="store.getters.getRole=='IP4'"><a href="#" @click="trasPage('registNotice')">お知らせ登録</a></li>
          <li v-show="store.getters.getRole=='IP4'"><a href="#" @click="trasPage('sendMail')">メール配信</a></li>
          <li v-show="store.getters.getRole=='IP4'"><a href="#" @click="trasPage('accountList')">ユーザ</a></li>
          <li v-show="store.getters.getRole=='IP4'"><a href="#" @click="trasPage('catalogList')">カタログ</a></li>
          <li><a href="#" @click.prevent.stop="viewChangePassword">パスワード変更</a></li>
          <li><a href="#" @click.prevent.stop="logout">ログアウト</a></li>
        </ul>
      </nav>
      </div>
      
	<div v-show="isEdit" style="position:absolute;  left:  800px; top: 0px;border :solid 2px; padding:10px;max-height:270px;width:400px;background:#fff;">
		<PasswordChange v-if="isEdit == true"  @setIsEdit="setIsEdit" ref="editCatalogComp"></PasswordChange>
	</div>
      
    </header>
    
    <router-view/>
    
    <footer :style="{ 'background-color': backgroundColor, 'background-size':'cover' }">
        <nav>
        <ul>
            <li><a href="https://www.jungleocean.com/trivia/【告知】2017年6月5日よりジャングルオーシャン本格" target="_blank">ジャングルオーシャンって？</a></li>
            <li><a href="https://www.jungleocean.com/programming/190311positionsticky/">記事ページに戻る</a></li>
        </ul>
    </nav>

    </footer>
  </div>
</template>

<script>

import accountRepository from './composables/AccountRepository'
import { useStore } from 'vuex'
import { ref } from 'vue'
import { useRouter  } from 'vue-router'
import PasswordChange from './components/PasswordChange'
//import titleImage from "@/assets/title.jpg"

export default ({
  name: 'App',
  components:{PasswordChange},
  setup(){
    const isEdit = ref(false)
    const { logoutAccount } = accountRepository()
    const backgroundImageSrc = ref( require('./assets/admin_inside.jpg') )
    const backgroundColor = ref(null)
    const titleImageSrc = ref( require('./assets/title.jpg') )
    //Vuex使用変数宣言
    const store = useStore()
    const router = useRouter()
    
    router.beforeEach((to, from, next) => {
 
      console.log(from )
      console.log( to )
      console.log( store.getters.getIsLogin )
      console.log( store.getters.getRole )
 
      if( to.path == "/" ){
        backgroundColor.value = "rgb(255, 230, 101)"
      }else if( to.path == "/accountList"){
        backgroundColor.value =  "rgb(153, 139, 175)"
      }else if( to.path == "/catalogList" ){
        backgroundColor.value = "rgb(255, 230, 101)"
      }else if( to.path == "/sendMail"){
        backgroundColor.value =  "rgb(153, 139, 175)"
      }else if( to.path == "/registNotice"){
        backgroundColor.value =  "rgb(153, 139, 175)" 
      }
 
      if( to.path == "/"){
        next()
        return
      }
 
      if( !store.getters.getIsLogin ){
        router.push("/")
      }else{
      var role = store.getters.getRole
      if( role == "guest" && ( to.path == "/accountList" || to.path == "/sendMail" || to.path == "/registNotice") ){
        router.push("/authorityError")
      }else{
        next()
      }
   }})


    //ここでページ毎にヘッダー色を変える
    const trasPage = ( page ) => {
      if( page == "registNotice"){
        backgroundImageSrc.value = require('./assets/admin_inside.jpg')
        backgroundColor.value = "rgb(153, 139, 175)"
        router.push("/registNotice")
      }
      else if( page == "sendMail"){
        backgroundImageSrc.value = require('./assets/admin_inside.jpg')
        backgroundColor.value = "rgb(153, 139, 175)"
        router.push("/sendMail")
      }else if( page == "accountList"){
        backgroundImageSrc.value = require('./assets/admin_inside.jpg')
        backgroundColor.value = "rgb(153, 139, 175)"
        router.push("/accountList")
      }else if( page == "catalogList"){
        backgroundImageSrc.value = require('./assets/admin_inside.jpg')
        backgroundColor.value = "rgb(255, 230, 101)"
        router.push("/catalogList")
      }
    }

    const logout = ()=>{
      logoutAccount( callback )
    }
    

    const callback = ()=>{
      backgroundColor.value = "rgb(255, 230, 101)"
      router.push("/")
    }
    

    
    const viewChangePassword = () =>{
      isEdit.value = true
    }
    
    const setIsEdit = ( value ) => {
      isEdit.value = value;
    }
    
    return{
      logout,
      backgroundImageSrc,
      isEdit,
      setIsEdit,
      viewChangePassword,
      store,
      backgroundColor,
      trasPage,
      titleImageSrc
    }
  }
})
</script>


