<template>

		<div ref="msgBoxDiv" style="background:#FFFFFF;padding:10px;height:80px;width:250px;border :solid 2px black;position:absolute;  left:  5px; top: 500px;">
			<center>
			<div style="color:red;">{{message}}</div>
			<div><br>
			<input type="button" value="はい" class="button" @click='returnResult( MSG_BOX_OK )'>
			<input type="button" value="いいえ" class="button" @click='returnResult( MSG_BOX_NG )'>
			</div>
			</center>
		</div>

</template>


<script>

import { ref, onMounted } from 'vue'

export default ({
  name: 'MsgBox',
  props:{
    targetElement:Object,
    message:String
  },
  setup(props, context){
    const MSG_BOX_OK = "OK"
    const MSG_BOX_NG = "NG"
    
    const msgBoxDiv = ref(null)
    console.log("Mounted")
    onMounted(()=>{
    console.log("Mounted")
      console.log( props.targetElement.getBoundingClientRect() )
      msgBoxDiv.value.style.left = props.targetElement.getBoundingClientRect().left + "px";
      
      msgBoxDiv.value.style.top = props.targetElement.getBoundingClientRect().top + props.targetElement.getBoundingClientRect().height + "px";
    })
    
    
    const returnResult = ( result ) => {
      console.log( result)
      context.emit( 'returnAction', result )
    }
    return{
      msgBoxDiv,
      returnResult,
      MSG_BOX_OK,
      MSG_BOX_NG
    }
  }
})


</script>