import { createRouter, createWebHistory } from 'vue-router'

import AccountList from '../views/AccountList.vue'
import Login from '../views/Login.vue'
import CatalogList from '../views/CatalogList.vue'
import SendMail from '../views/SendMail.vue'
import RegistNotice from '../views/RegistNotice.vue'
import AuthorityError from '../views/AuthorityError.vue'


import store from '@/store/index';

const routes = [
  {
    path: '/',
    name: 'login',
    component: Login
  },
  {
    path: '/accountList',
    name: 'accountList',
    component: AccountList
  },

  {
    path: '/catalogList',
    name: 'catalogList',
    component: CatalogList
  },
  {
    path: '/sendMail',
    name: 'sendMail',
    component: SendMail
  },
  {
    path: '/registNotice',
    name: 'registNotice',
    component: RegistNotice
  },
  {
    path: '/authorityError',
    name: 'authorityError',
    component: AuthorityError
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
});


router.beforeEach((to, from, next) => {
 
 console.log(from )
 console.log( to )
 console.log( store.getters.getIsLogin )
 console.log( store.getters.getRole )
 
 if( to.path == "/"){
   next()
   return
 }
 
 if( !store.getters.getIsLogin ){
   router.push("/")
 }else{
   var role = store.getters.getRole
   if( role == "guest" && ( to.path == "/accountList" || to.path == "/sendMail" || to.path == "/registNotice") ){
     router.push("/authorityError")
   }else{
     next()
   }
 }
})



export default router



