let receiveAccountListFile = ( progressCallback ) =>{
	var authToken = store.getters.getAuthToken
    return new Promise((resolve, reject) => {
      
      axios
        .get(url + '/api/account/receivefile', {
            onDownloadProgress: progressCallback,
            headers: {
                "X-AUTH-TOKEN" : authToken
            },
            responseType: "blob",
        } )
        .then(response => {
            console.log("down")
            let mineType = response.headers["content-type"];
            console.log( response.headers["content-disposition"] )
            const name = getFileName(  response.headers["content-disposition"] ) 
            const blob = new Blob([response.data], { type: mineType });
            saveAs(blob, name);
            console.log("end")
           resolve(response.data)
         }).catch(error => {
            reject(error)
         })
    }).catch((e) => {
      throw e
    })
}


let receiveCatalogFile = ( id, waterMark, progressCallback ) =>{
	var authToken = store.getters.getAuthToken
    return new Promise((resolve, reject) => {
      
     const formData = new FormData()
     formData.append('id', id)
     formData.append('waterMark', waterMark)
      
      axios
        .post(url + '/api/catalog/receivefile', formData, {
            onDownloadProgress: progressCallback,
            headers: {
                "X-AUTH-TOKEN" : authToken
            },
            responseType: "blob",
        } )
        .then(response => {
         console.log("down")
            let mineType = response.headers["content-type"];
            console.log( response.headers["content-disposition"] )
            const name = getFileName(  response.headers["content-disposition"] ) 
            const blob = new Blob([response.data], { type: mineType });
            saveAs(blob, name);
           
           resolve(response.data)
         }).catch(error => {
            reject(error)
         })
    }).catch(() => {
      throw 'サーバアクセスエラー'
    })
}


function getFileName(contentDisposition){
    console.log( contentDisposition )
    let fileName = contentDisposition.substring(21,
                                                contentDisposition.length
                                                );
    //デコードするとスペースが"+"になるのでスペースへ置換します
    fileName = decodeURI(fileName).replace(/\+/g, " ");

    return fileName;
}

let loginByLoginForm = (loginForm) => {
  return new Promise((resolve, reject) => {
      axios
        .post( url  + '/api/login', loginForm )
        .then(response => {
           console.log( store.state.isLogin )
           store.commit("setAuthToken", response.headers['x-auth-token'])
           
           resolve(response.data)
         }).catch(error => {
            reject(error)
         })
    }).catch((e) => {
      console.log(e)
      throw e
    })
    
}

