let sendCatalog = (file, catalog, uploadProgressCallback) => {
  console.log(catalog)
  
  var authToken = store.getters.getAuthToken
 
  return new Promise((resolve, reject) => {
      const formData = new FormData()
      formData.append('file', file)
      formData.append('catalog', new Blob([JSON.stringify(catalog)], {type : 'application/json'}))

      axios
        .post(url + '/api/catalog/regist', formData, 
        { 
            onUploadProgress: uploadProgressCallback, 
            headers: {
                "X-AUTH-TOKEN" : authToken
            }
        
        } )
        .then(response => {
           resolve(response.data)
         }).catch(error => {
            reject(error)
         })
    }).catch((e) => {
      throw e
    })
}

