import { createStore } from 'vuex'
import createPersistedState from 'vuex-persistedstate'


export default createStore({
  state: {
    searchAccountId:'',
    searchAccountName:'',
    searchCompanyName:'',
    isLogin:false,
    authToken:'',
    role:'',
    currentView:''
  },
  mutations: {
    setSearchAccountId(state, value){
      state.searchAccountId = value
    },
    setSearchAccountName(state, value){
      state.searchAccountName = value
    },
    setSearchCompanyName(state, value){
      state.searchCompanyName = value
    },
    setAuthToken(state, value){
      state.authToken = value
    },
    setIsLogin(state, value){
      state.isLogin = value
    },
    setRole(state, value){
      state.role = value
    }
  },
  actions: {
    setSearchAccountId: function(commit, value) {
      commit('setSearchAccountId', value)
    },
    setSearchAccountName: function(commit, value) {
      commit('setSearchAccountName', value)
    },
    setSearchCompanyName: function(commit, value) {
      commit('setSearchCompanyName', value)
    },
    setAuthToken: function(commit, value) {
      commit('setAuthToken', value)
    },
    setIsLogin: function(commit, value) {
      commit('setIsLogin', value)
    },
    setRole: function(ctx, value) {
      ctx.commit('setRole', value)
    }
  },
  getters:{
    getAuthToken(state){
      return state.authToken
    },
    getIsLogin(state){
      return state.isLogin
    },
    getRole(state){
      return state.role
    }
  },
  
  plugins: [createPersistedState(
    { 
      storage: window.sessionStorage
    }
  )]
  
})
